export default "<span class=\"hljs-keyword\">import</span> { Component, ChangeDetectionStrategy } from <span class=\"hljs-string\">'@angular/core'</span>;\n<span class=\"hljs-keyword\">import</span> {\n  CalendarDateFormatter,\n  CalendarEvent,\n  CalendarView,\n  DAYS_OF_WEEK,\n} from <span class=\"hljs-string\">'angular-calendar'</span>;\n<span class=\"hljs-keyword\">import</span> { CustomDateFormatter } from <span class=\"hljs-string\">'./custom-date-formatter.provider'</span>;\n\n@Component({\n  selector: <span class=\"hljs-string\">'mwl-demo-component'</span>,\n  changeDetection: ChangeDetectionStrategy.OnPush,\n  templateUrl: <span class=\"hljs-string\">'template.html'</span>,\n  providers: [\n    {\n      provide: CalendarDateFormatter,\n      useClass: CustomDateFormatter,\n    },\n  ],\n})\n<span class=\"hljs-keyword\">export</span> <span class=\"hljs-keyword\">class</span> DemoComponent {\n  view: CalendarView = CalendarView.Month;\n\n  viewDate = <span class=\"hljs-keyword\">new</span> <span class=\"hljs-built_in\">Date</span>();\n\n  events: CalendarEvent[] = [];\n\n  locale: <span class=\"hljs-built_in\">string</span> = <span class=\"hljs-string\">'fr'</span>;\n\n  weekStartsOn: <span class=\"hljs-built_in\">number</span> = DAYS_OF_WEEK.MONDAY;\n\n  weekendDays: <span class=\"hljs-built_in\">number</span>[] = [DAYS_OF_WEEK.FRIDAY, DAYS_OF_WEEK.SATURDAY];\n\n  CalendarView = CalendarView;\n\n  setView(view: CalendarView) {\n    <span class=\"hljs-keyword\">this</span>.view = view;\n  }\n}\n";